html {
  overflow-x: hidden;
}

html, body {
  height: inherit;
}

body {
  font-family: $sans;
  font-size: 16px;
  font-weight: $m-font-weight;
  line-height: $m-line-height;
  color: $p100;
  background-color: $p500;
  margin: 0;
  padding: 0;
  min-height: 100vh;
  position: relative;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  -webkit-backface-visibility: hidden;

  &.gradient {
    &:before {
      content: "";
      display: block;
      position: fixed;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      background: radial-gradient(ellipse at center, rgba(76,76,76,1) 0%,rgba(19,19,19,1) 100%);
    }
  }
}


::selection {
  background-color: $p700;
  text-shadow: none;
}

::-moz-selection {
  background-color: $p700;
  text-shadow: none;
}

::-webkit-input-placeholder { color: $p300; opacity: .7; }
::-moz-placeholder { color: $p300; opacity: .7; }
:-ms-input-placeholder { color: $p300; opacity: .7; }
input:-moz-placeholder { color: $p300; opacity: .7; }

a {
  text-decoration: none;
  @include text-general-black($p100);
  font-weight: 500;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

.rel {
  position: relative;
}

.container_styleguide {
  margin: 10px 0;
  span {
    margin-right: 10px;
  }
}

#container {
  padding-top: 130px;
  padding-bottom: 90px;
}

// .page {
