// COMPONENTS
nav {
  ul {
    list-style: none;
    padding: 0;
    margin: 0;
    li {
      float: left;
      margin-right: 40px;
      &:last-of-type {
        margin-right: 0;
      }
    }
  }
}


.App {
  &.newContract {
    #secondary_header {
      display: none;
    }
    .page-newContract {
      #secondary_header {
        display: block;
        .menu_title {
          @include section-title();
          color: $p900;
          margin-right: 30px;
        }
      }
    }
  }
}

.loader_active {
  #sidebar_management {
    top: 70px;
  }
  #wrapper {
    top: 70px;
  }
  #secondary_header {
    top: 70px;
  }
}

.wrapper_inactive {
  position: fixed;
  top: 70px;
  left: 0;
  right: 0;
  background-color: transparent;
  cursor: wait;
  /* opacity: 0.9; */
  bottom: 0;
  z-index: 9;
}

.index_wrapper {
  // background: url('../images/bgspace.png');
  background-size: cover;
  background-repeat: no-repeat;
  //height: 100vh;
}

.row.full {
  max-width: 100%;
}

