.identityManager {
  position: relative;
  .selectOrchestrator_overlay {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-color: rgba(0,0,0,.2);
    z-index: 999;
    display: none;
    &.active {
      display: block;
    }
  }
  .select_orchestrator {
    position: absolute;
    left: 50%;
    width: 400px;
    margin-left: -200px;
    margin-top: 200px;
    background: #FFFFFF;
    z-index: 99999;
    top: 0;
    border-radius: 2px;
    box-shadow: 0 2px 1px rgba(0,0,0,.2);
    display: none;
    &.active {
      display: block;
    }
    .orchestrator_wrapper {
      padding: 20px;
      position: relative;
      .close {
        position: absolute;
        right: -15px;
        background: #929396;
        width: 30px;
        height: 30px;
        text-align: center;
        top: -15px;
        border-radius: 100px;
        i {
          height: 30px;
          line-height: 30px;
        }
      }
      h3 {
        color: #333;
        font-size: 20px;
        font-weight: 500;
      }
      select {
        background: #f7f7f7;
        margin-top: 20px;
        color: #333;
        border: 1px solid #C6C6C6;
        text-indent: 5px;
        border-radius: 2px;
      }
      button {
        height: 30px;
        &:last-of-type {
          margin-left: 10px;
          background: #F2F2F2;
          color: #333;
        }
      }
    }
  }
}


