.wrapper_qrcode {
  margin-top: 20px;
  position: relative;
  z-index: 10;
  .qrcode_title {
    @include text-title-regular($p100);
    text-align: center;
    .title_release {
      border-radius: 2px;
      border: 1px solid white;
      padding: 2px 10px;
      margin-left: 10px;
      @include text-mini-black($p100);
      vertical-align: middle;
    }
  }
  .disclaimer {
    @include text-general($p100);
    text-align: center;
    font-size: 18px;
    margin-top: 30px;
    border: solid thin white;
    padding: 4px;
  }

  .qrcode_tagline {
    @include text-general($p100);
    text-align: center;
    font-size: 16px;
    margin-top: 30px;
    line-height: 28px;
  }

  .qrcode_instruction {
      @include text-general($p100);
      text-align: center;
  }

  .qrcode_version {
    height: 30px;
    background: $red;
    border-radius: 2px;
    @include text-general-black($p100);
    width: 80px;
    margin: 0 auto;
    margin-top: 20px;
    line-height: 30px;
    text-align: center;
  }

  .qrcode_body {
    margin: 0 auto;
    text-align: center;
    margin-top: 40px;
    margin-bottom: 40px;
    img {
      border-radius: 5px;
      box-shadow: 0 10px 14px 1px rgba(0, 0, 0, 0.5);
    }
  }
}

.header {
  .infoDataPanelOverlay {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-color: rgba(0,0,0,.2);
    z-index: 999;
    display: none;
    &.active {
      display: block;
    }
  }
  .infoDataPanel {
    color: black;
    position: absolute;
    left: 50%;
    width: 580px;
    margin-left: -270px;
    margin-top: 200px;
    background: #FFFFFF;
    z-index: 99999;
    top: 0;
    border-radius: 2px;
    box-shadow: 0 2px 1px rgba(0,0,0,.2);
    display: none;
    &.active {
      display: block;
    }
    .data_fields {
      margin-top: 30px;
    }
    .field {
      font-weight: bold
    }
    .row {
      margin-bottom: 6px;
    }
    .infoData_wrapper {
      padding: 20px;
      position: relative;
      .close {
        position: absolute;
        right: -15px;
        background: #929396;
        width: 30px;
        height: 30px;
        text-align: center;
        top: -15px;
        border-radius: 100px;
        i {
          height: 30px;
          line-height: 30px;
        }
      }
      h3 {
        color: #333;
        font-size: 20px;
        font-weight: 500;
      }
    }
  }
}


