.button, button, input[type="submit"] {
  @include text-general-black($p100);
  font-size: 12px;
  text-align: center;
  display: block;
  height: 30px;
  line-height: 30px;
  display: inline-block;
  background-color: $p700;
  padding: 0;
  @include transition(.2s);
  border-radius: 3px;
  cursor: pointer;
  padding-right: 10px;
  padding-left: 10px;
  position: relative;
  width: auto;
  span {
    text-align: left;
    margin-left: 10px;
    margin-right: 10px;
    border-right: 1px solid rgba(245, 245, 245, .2);
    text-align: center;
    width: 40px;
    display: inline-block;
    margin-left: -10px;
    padding: 0;
  }
  &.spaced-left {
    margin-left: 10px;
  }
  &.spaced-leftX2 {
    margin-left: 20px;
  }
  &.spaced-right {
    margin-right: 10px;
  }
  &.spaced-rightX2 {
    margin-right: 20px;
  }
  &.spaced-top {
    margin-top: 10px;
  }
  &.spaced-topX2 {
    margin-top: 20px;
  }
  &.spaced-bottom {
    margin-bottom: 10px;
  }
  &.spaced-bottomX2 {
    margin-bottom: 20px;
  }
  &:hover {
    background-color: $p300;
  }
  &:focus {
    outline: none;
  }
  &:active {
    @include scale(0.95);
  }
  &.bordered {
    border: 1px solid white;
  }
  &.line {
    background-color: transparent;
    border: 2px solid $p100;
    line-height: 36px;
    &:hover {
      background-color: $p300;
    }
  }
  &.evil {
    background-color: $red;
    &:hover {
      //background-color: $;
    }
  }
  &.good {
    background-color: $accent-500;
    &:hover {
      background-color: $accent-700;
    }
  }
  &.mini {
    height: 30px;
    line-height: 30px;
  }
}

input[type="submit"] {
  border: none;
}

.button-control {
  width: 30px;
  height: 30px;
  border: 2px solid $p200;
  text-align: center;
  font-size: 18px;
  line-height: 28px;
  cursor: pointer;
  @include circle;
  @include transition(.1s);
  &.big {
    width: 50px;
    height: 50px;
    font-size: 40px;
    line-height: 52px;
    text-indent: 4px;
  }
  &:hover {
    border: 2px solid $p100;
  }
}



