.filters {
  height: 50px;
  line-height: 50px;
  // position: fixed;
  margin-left: 20px;
  left: 230px;
  right: 0;
  z-index: $zmedium;
  top: 280px;
  @include clearfix;
  .filters_title {
    display: inline-block;
    @include clearfix;
    padding-right: 30px;
    position: relative;
    .icon-magnifying-glass {
      position: absolute;
      z-index: 20;
      right: 10px;
      line-height: 40px;
      cursor: pointer;
      @include transition(.3s);
    }
    .title {
      @include text-general-black($p100);
      display: inline-block;
      margin-left: 10px;
      margin-right: 30px;
    }
  }
  .filters_list {
    display: inline-block;
    border-left: $separator;
    height: 50px;
    select {
      height: 50px;
      background: transparent;
      border: 0;
      border-radius: 0;
      padding: 0 10px;
      width: 160px;
    }
    .filters_type {
      float:left;
    }
  }
  .filters_search {
    float: left;
    @include clearfix;
    width: 280px;
    overflow: visible;
    visibility: visible;
    opacity: 1;
    input {
      height: 30px;
      margin: 0;
      text-indent: 0;
      border-bottom: 0;
      padding-left: 10px;
      @include transition(.3s);
      border: 1px solid rgba(0, 0, 0, 0.06);
      box-shadow: 0 2px 3px 0 rgba(0, 0, 0, 0.2);
      border-radius: 4px;
      height: 40px;
      padding: 10px;
      width: 320px;
      background: none;
      line-height: 40px;
      color: $p100;
      &::-webkit-input-placeholder { /* Chrome/Opera/Safari */
        color: $p100;
      }
      &::-moz-placeholder { /* Firefox 19+ */
        color: $p100;
      }
      &:-ms-input-placeholder { /* IE 10+ */
        color: $p100;
      }
      &:-moz-placeholder { /* Firefox 18- */
        color: $p100;
      }
    }
  }

  .add_nodes, .delete_nodes, .add_contract, .delete_contracts {
    float: right;
    width: 140px;
    @include text-general-black($p100);
    margin-bottom: 0px;
    height: 25px;
    background-color: #333d49;
    border-radius: 2px;
    font-size: 12px;
    font-weight: bold;
    color: #f5f5f5;
    letter-spacing: 2px;
    line-height: 25px;
    text-transform: initial;
    display: inline-block;
    width: 140px;
    margin-right: 20px;
    border-radius: 100px;
    margin-left: 0;
    .nodes_number, .contracts_number {
      color: #f6b000;
      display: inline-block;
      margin-right: 8px;
      margin-left: -13px;
      margin-top: -4px;
      line-height: 19px;
      padding: 0px 0px;
      font-weight: 800;
      text-align: center;
      vertical-align: initial;
      font-size: 12px;
    }
  }
  .delete_contracts {
    width: 190px !important;
    background: none;
    border:1px solid $p100;
    margin-right: 20px;
  }
  .add_contract {
    background: $green;
    text-align: center;
  }

}

.contract_profile {
  .profile_add {
    float: left;
  }
  .profile_remove {
    float: left;
  }
}

.filter_contractType {
  padding-right: 30px;
  position: relative;
  width: 300px;
  display: inline-block;
  top: -10px;
  select {
    margin: 0;
    padding: 0;
    background-color: $p700;
    height: 30px;
    border: 0;
    border-radius: 0px !important;
    -webkit-appearance: none;
    -webkit-border-radius: 0px;
  }
}

.filter_buttons {
  float: right;
}

/**
 * React Select
 * ============
 * Created by Jed Watson and Joss Mackison for KeystoneJS, http://www.keystonejs.com/
 * https://twitter.com/jedwatson https://twitter.com/jossmackison https://twitter.com/keystonejs
 * MIT License: https://github.com/JedWatson/react-select
*/
.Select {
  position: relative;
}
.Select,
.Select div,
.Select input,
.Select span {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
.Select.is-disabled > .Select-control {
  background-color: #f9f9f9;
}
.Select.is-disabled > .Select-control:hover {
  box-shadow: none;
}
.Select.is-disabled .Select-arrow-zone {
  cursor: default;
  pointer-events: none;
  opacity: 0.35;
}
.Select-control {
  background-color: #fff;
  border-color: #d9d9d9 #ccc #b3b3b3;
  border-radius: 4px;
  border: 1px solid #ccc;
  color: #333;
  cursor: default;
  display: table;
  border-spacing: 0;
  border-collapse: separate;
  height: 36px;
  outline: none;
  overflow: hidden;
  position: relative;
  width: 100%;
}
.Select-control:hover {
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.06);
}
.Select-control .Select-input:focus {
  outline: none;
}
.is-searchable.is-open > .Select-control {
  cursor: text;
}
.is-open > .Select-control {
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
  background: #fff;
  border-color: #b3b3b3 #ccc #d9d9d9;
}
.is-open > .Select-control > .Select-arrow {
  border-color: transparent transparent #999;
  border-width: 0 5px 5px;
}
.is-searchable.is-focused:not(.is-open) > .Select-control {
  cursor: text;
}
.is-focused:not(.is-open) > .Select-control {
  border-color: #007eff;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 0 3px rgba(0, 126, 255, 0.1);
}
.Select-placeholder,
.Select--single > .Select-control .Select-value {
  bottom: 0;
  color: #aaa;
  left: 0;
  line-height: 34px;
  padding-left: 10px;
  padding-right: 10px;
  position: absolute;
  right: 0;
  top: 0;
  max-width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.has-value.Select--single > .Select-control .Select-value .Select-value-label,
.has-value.is-pseudo-focused.Select--single > .Select-control .Select-value .Select-value-label {
  color: #333;
  // color: #f1f1f1;
  font-size: 14px;
  line-height: 36px;
}
.has-value.Select--single > .Select-control .Select-value a.Select-value-label,
.has-value.is-pseudo-focused.Select--single > .Select-control .Select-value a.Select-value-label {
  cursor: pointer;
  text-decoration: none;
}
.has-value.Select--single > .Select-control .Select-value a.Select-value-label:hover,
.has-value.is-pseudo-focused.Select--single > .Select-control .Select-value a.Select-value-label:hover,
.has-value.Select--single > .Select-control .Select-value a.Select-value-label:focus,
.has-value.is-pseudo-focused.Select--single > .Select-control .Select-value a.Select-value-label:focus {
  color: #007eff;
  outline: none;
  text-decoration: underline;
}
.Select-input {
  height: 34px;
  padding-left: 10px;
  padding-right: 10px;
  vertical-align: middle;
}
.Select-input > input {
  width: 100%;
  background: none transparent;
  border: 0 none;
  box-shadow: none;
  cursor: default;
  display: inline-block;
  font-family: inherit;
  font-size: inherit;
  margin: 0;
  outline: none;
  line-height: 14px;
  /* For IE 8 compatibility */
  padding: 8px 0 12px;
  /* For IE 8 compatibility */
  -webkit-appearance: none;
}
.is-focused .Select-input > input {
  cursor: text;
}
.has-value.is-pseudo-focused .Select-input {
  opacity: 0;
}
.Select-control:not(.is-searchable) > .Select-input {
  outline: none;
}
.Select-loading-zone {
  cursor: pointer;
  display: table-cell;
  position: relative;
  text-align: center;
  vertical-align: middle;
  width: 16px;
}
.Select-loading {
  -webkit-animation: Select-animation-spin 400ms infinite linear;
  -o-animation: Select-animation-spin 400ms infinite linear;
  animation: Select-animation-spin 400ms infinite linear;
  width: 16px;
  height: 16px;
  box-sizing: border-box;
  border-radius: 50%;
  border: 2px solid #ccc;
  border-right-color: #333;
  display: inline-block;
  position: relative;
  vertical-align: middle;
}
.Select-clear-zone {
  -webkit-animation: Select-animation-fadeIn 200ms;
  -o-animation: Select-animation-fadeIn 200ms;
  animation: Select-animation-fadeIn 200ms;
  color: #999;
  cursor: pointer;
  display: table-cell;
  position: relative;
  text-align: center;
  vertical-align: middle;
  width: 17px;
}
.Select-clear-zone:hover {
  color: #D0021B;
}
.Select-clear {
  display: inline-block;
  font-size: 18px;
  line-height: 1;
}
.Select--multi .Select-clear-zone {
  width: 17px;
}
.Select-arrow-zone {
  cursor: pointer;
  display: table-cell;
  position: relative;
  text-align: center;
  vertical-align: middle;
  width: 25px;
  padding-right: 5px;
}
.Select-arrow {
  border-color: #999 transparent transparent;
  border-style: solid;
  border-width: 5px 5px 2.5px;
  display: inline-block;
  height: 0;
  width: 0;
}
.is-open .Select-arrow,
.Select-arrow-zone:hover > .Select-arrow {
  border-top-color: #666;
}
.Select--multi .Select-multi-value-wrapper {
  display: inline-block;
}
.Select .Select-aria-only {
  display: inline-block;
  height: 1px;
  width: 1px;
  margin: -1px;
  clip: rect(0, 0, 0, 0);
  overflow: hidden;
}
@-webkit-keyframes Select-animation-fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes Select-animation-fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.Select-menu-outer {
  border-bottom-right-radius: 4px;
  border-bottom-left-radius: 4px;
  background-color: #fff;
  border: 1px solid #ccc;
  border-top-color: #e6e6e6;
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.06);
  box-sizing: border-box;
  margin-top: -1px;
  max-height: 200px;
  position: absolute;
  top: 100%;
  width: 100%;
  left: 0;
  right: 0;
  z-index: 1;
  -webkit-overflow-scrolling: touch;
}
.Select-menu {
  max-height: 198px;
  overflow-y: auto;
}
.Select-option {
  box-sizing: border-box;
  background-color: #fff;
  color: #666666;
  cursor: pointer;
  display: block;
  padding: 8px 10px;
}
.Select-option:last-child {
  border-bottom-right-radius: 4px;
  border-bottom-left-radius: 4px;
}
.Select-option.is-selected {
  background-color: #f5faff;
  /* Fallback color for IE 8 */
  background-color: rgba(0, 126, 255, 0.04);
  color: #333;
}
.Select-option.is-focused {
  background-color: #ebf5ff;
  /* Fallback color for IE 8 */
  background-color: rgba(0, 126, 255, 0.08);
  color: #333;
}
.Select-option.is-disabled {
  color: #cccccc;
  cursor: default;
}
.Select-noresults {
  box-sizing: border-box;
  color: #999999;
  cursor: default;
  display: block;
  padding: 8px 10px;
}
.Select--multi .Select-input {
  vertical-align: middle;
  margin-left: 10px;
  padding: 0;
}
.Select--multi.has-value .Select-input {
  margin-left: 5px;
}
.Select--multi .Select-value {
  background-color: #ebf5ff;
  /* Fallback color for IE 8 */
  background-color: rgba(0, 126, 255, 0.08);
  border-radius: 2px;
  border: 1px solid #c2e0ff;
  /* Fallback color for IE 8 */
  border: 1px solid rgba(0, 126, 255, 0.24);
  color: #007eff;
  display: inline-block;
  font-size: 0.9em;
  line-height: 1.4;
  margin-left: 5px;
  margin-top: 5px;
  vertical-align: top;
}
.Select--multi .Select-value-icon,
.Select--multi .Select-value-label {
  display: inline-block;
  vertical-align: middle;
}
.Select--multi .Select-value-label {
  border-bottom-right-radius: 2px;
  border-top-right-radius: 2px;
  cursor: default;
  padding: 2px 5px;
}
.Select--multi a.Select-value-label {
  color: #007eff;
  cursor: pointer;
  text-decoration: none;
}
.Select--multi a.Select-value-label:hover {
  text-decoration: underline;
}
.Select--multi .Select-value-icon {
  cursor: pointer;
  border-bottom-left-radius: 2px;
  border-top-left-radius: 2px;
  border-right: 1px solid #c2e0ff;
  /* Fallback color for IE 8 */
  border-right: 1px solid rgba(0, 126, 255, 0.24);
  padding: 1px 5px 3px;
}
.Select--multi .Select-value-icon:hover,
.Select--multi .Select-value-icon:focus {
  background-color: #d8eafd;
  /* Fallback color for IE 8 */
  background-color: rgba(0, 113, 230, 0.08);
  color: #0071e6;
}
.Select--multi .Select-value-icon:active {
  background-color: #c2e0ff;
  /* Fallback color for IE 8 */
  background-color: rgba(0, 126, 255, 0.24);
}
.Select--multi.is-disabled .Select-value {
  background-color: #fcfcfc;
  border: 1px solid #e3e3e3;
  color: #333;
}
.Select--multi.is-disabled .Select-value-icon {
  cursor: not-allowed;
  border-right: 1px solid #e3e3e3;
}
.Select--multi.is-disabled .Select-value-icon:hover,
.Select--multi.is-disabled .Select-value-icon:focus,
.Select--multi.is-disabled .Select-value-icon:active {
  background-color: #fcfcfc;
}
@keyframes Select-animation-spin {
  to {
    transform: rotate(1turn);
  }
}
@-webkit-keyframes Select-animation-spin {
  to {
    -webkit-transform: rotate(1turn);
  }
}


 .list_filter {
    margin-top: 50px;
    border: 1px solid rgba(0, 0, 0, 0.1);
    background: white;
    border-radius: 2px;
    height: 70px;
    @include clearfix;
    .button {
      height: 40px;
      width: 110px;
      font-size: 13px;
      line-height: 40px;
      margin-top: 15px;
      padding: 0;
      float: right;
      letter-spacing: 0;
      background-image: linear-gradient( #C6426E, #BD3F32);
      &.bigger {
        width: 180px;
      }
    }
    .filter_item {
      border-right: 1px solid rgba(0, 0, 0, 0.1);
      padding: 10px;
      height: 70px;
      &.last {
          border-right: 0;
        }
      select {
        text-indent: 0;
        padding: 0;
        margin: 0;
        border: 0;
        @include text-mini($p900);
        outline: none;
        -webkit-appearance: none;
        -moz-appearance:    none;
        appearance:         none;
        text-overflow: '';
        background: url("../images/select_arrow.svg") no-repeat right center;
        height: 30px;
        margin-left: 5px;
        text-transform: capitalize;
      }
      label {
        display: block;
        @include text-mini-black($p900);
        padding-left: 10px;
        text-transform: uppercase;
      }
      input {
        background: transparent;
        border: none;
        margin-top: 7px;
        width: 100%;
        color: $p900;
        font-weight: 400;
        text-indent: 10px;
        &::-webkit-input-placeholder { /* Chrome/Opera/Safari */
          @include text-mini($p900);
        }
        &::-moz-placeholder { /* Firefox 19+ */
          @include text-mini($p900);
        }
        &:-ms-input-placeholder { /* IE 10+ */
          @include text-mini($p900);
        }
        &:-moz-placeholder { /* Firefox 18- */
          @include text-mini($p900);
        }
      }

    }
  }

