.home {
  margin-left: 30px;
  margin-top: 20px;
  margin-right: 30px;
  .home_title {
    @include text-title-light($p100);
  }
  .container_context {
    margin-top: 40px;
    padding-bottom: 20px;
    cursor: pointer;
    .context_name {
      @include section-title;
    }
    .context_notification {
      margin-top: 20px;
      .icon-rocket {
        color: $p100;
        margin-right: 15px;
      }
      h4 {
        @include text-general($p100);
        display: inline-block;
        span {
          font-weight: bold;
          position: relative;
          &:after {
            position: absolute;
            content: "";
            width: 100%;
            background: $p100;
            height: 1px;
            bottom: -3px;
            left: 0;
          }
        }
      }
    }
    .context_new {
      margin-top: 40px;
      .new_title {
        @include list_title($p900);
      }
      .filters {
        margin-bottom: 10px;
        margin-left: 0px;
        margin-top: 40px;
      }
    }

    .flex_table {
      // margin-left: 0;
      // padding-top: 40px;
    }
  }
}



.console_background {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: $p700;
  z-index: 1;
}

.console {
  padding-top: 60px;
  position: relative;
  z-index: 10;
  .console_title {
    margin-top: 50px;
    margin-bottom: 40px;
    @include text-title-regular(#53575A);
    font-size: 16px;
    text-align: left;
    font-weight: 400;
    letter-spacing: .5px;
    position: relative;
    &:after {
      content: "";
      position: absolute;
      bottom: -10px;
      left: 0;
      width: 100%;
      height: 1px;
      background-color: #D4D4D4;
      display: block;
    }

  }
  .console_tagline {
    @include text-mini-title(#53575A);
    margin-top: 10px;
  }
  .console_app {
    // margin-top: 100px;
    // .medium-4.end .data_unit {
    //   background-image: linear-gradient( #C6426E, #BD3F32);
    // }
    .header_data {
      margin-top: 0;
    }
    .app_item {
      width: 100%;
      // height: 70px;
      background: #f9f9f9;
      border-radius: 3px;
      cursor: pointer;
      border-radius: 3px;
      @include transition(.3s);
      margin-bottom: 20px;
      border: 1px dashed #c9c9c9;
      &:hover {
        background: #CCCCCC;
      }
      .item_first {
        @include clearfix;
      }
      p {
        font-size: 14px;
        margin: 0;
        color: #787C7F;
        margin-left: 20px;
        margin-bottom: 10px;
        font-weight: 300;
      }
      span {
        font-size: 20px;
        float: left;
        color: #53575A;
        line-height: 50px;
        margin-left: 20px;
        vertical-align: middle;
        margin-top: 14px;
         &.shield {
            background: url('../images/shield2.svg');
            width: 21px;
            height: 21px;
            background-size: cover;
            background-repeat: no-repeat;
          }
          &.target {
            background: url('../images/target.svg');
            width: 21px;
            height: 21px;
            background-size: cover;
            background-repeat: no-repeat;
          }
          &.list {
            background: url('../images/list.svg');
            width: 21px;
            height: 21px;
            background-size: cover;
            background-repeat: no-repeat;
          }
      }
      .item_title {
        @include text-general-black(#252729);
        font-size: 18px;
        font-weight: 500;
        margin-left: 20px;
        float: left;
        line-height: 50px;
        margin-bottom: 0;
      }
    }
  }
  .console_explanation {
    margin-top: 100px;
    background: white;
    padding: 20px;
    border-radius: 3px;
    box-shadow: 0 2px 4px rgba(0,0,0,.2);
    .explanation_title {
      @include text-mini-title($p900);
    }
    .explanation_description {
      @include text-general($p900);
    }
    .explanation_features {
      @include text-mini-black($p900);
      margin-top: 20px;
    }
    .explanation_open {
      width: auto;
      padding: 0 40px;
      margin-top: 20px;
      margin-bottom: 0;
    }
    .explanation_list {
      padding: 0;
      list-style: none;
      margin-top: 10px;
      .list_item {
        border: 1px solid $p700;
        @include text-mini($p900);
        border-radius: 2px;
        margin-bottom: 5px;
        padding: 0px 5px;
      }
    }
  }
}



