  .notification_dropdown  {
    background: #fff;
    padding: 0 5px;
    width: 240px;
    position: absolute;
    margin-left: -120px;
    left: 50%;
    top: 40px;
    border-radius: 2px;
    box-shadow: 0 2px 2px 0px rgba(21, 26, 29, 0.33);
    opacity: 0;
    visibility: hidden;
    top: 20px;
    border: 1px solid rgba(0, 0, 0, 0.04);
    @include transition(.3s);
    &:before {
      content: "";
      position: absolute;
      width: 0;
      height: 0;
      border-left: 6px solid transparent;
      border-right: 6px solid transparent;
      border-bottom: 8px solid #8c8c8c;
      top: -8px;
      right: 50%;
      margin-right: -4px;
    }
    &:after {
      content: "";
      position: absolute;
      width: 0;
      height: 0;
      border-left: 6px solid transparent;
      border-right: 6px solid transparent;
      border-bottom: 8px solid #fff;
      top: -8px;
      right: 50%;
      margin-right: -4px;
    }
    .dropdown_title {
      @include text-mini-black($p100);
      background: white;
      text-align: center;
    }
    .dropdown_list {
      padding: 0;
      margin: 0;
      list-style: none;
      // padding: 5px 0;
      overflow-y: scroll;
      max-height: 500px;
      .list_item {
        @include text-mini-black(#74787b);
        padding: 10px 15px;
        margin: 0;
        font-weight: 500;
        @include transition(.3s);
        cursor: pointer;
        border-bottom: 1px solid #e2e2e2;
        position: relative;
        letter-spacing: 0.5px;
        font-size: 12px;
        span {
          padding: 0 5px;
          border-radius: 20px;
          margin-right: 5px;
          background: #DADADA;
          font-weight: 700;
          font-size: 11px;
          text-transform: uppercase;
          color: #686868;
          &.warn {
            background: #FFAB00;
            color: white;
          }
          &.error {
            background: #FF5630;
            color: white;
          }
          &.success {
            background: #36B37E;
            color: white;
          }
        }
        a {
          color: $p700;
        }
        &:last-of-type {
          border-bottom: none;
        }
        &:hover {
          &:before {
              @include transition(.3s);
              content: "";
              position: absolute;
              width: 5px;
              /* height: 100%; */
              background: #f6b000;
              left: -6px;
              top: 10px;
              bottom: 10px;
          }
        }
      }
    }
    .notification_clear {
      height: 30px;
      line-height: 30px;
      text-align: center;
      font-size: 12px;
      font-weight: 600;
      letter-spacing: .5px;
      background: #f2f2f2;
      color: #818181;
      margin-left: -5px;
      margin-right: -5px;
      border-top: 1px solid #eaeaea;
      cursor: pointer;
    }
  }


