.header {
    margin: auto !important;
    .row.full {
      padding-left: 10px;
      margin-right: 0 !important;
      margin-left: 0 !important;
      .first {
        padding-left: 0;
      }
      .end {
        padding-right: 0;
        margin-right: 0;
      }
    }
    .header_data {
      margin-top: 50px;
      .data_header {
        @include clearfix;
        padding-left: 10px;
        margin-bottom: 10px;
        .header_title {
          font-size: 27px;
          font-weight: 200;
          letter-spacing: 1px;
          color: #262f39;
          float: left;
          line-height: 60px;
          margin-left: 0px;
          font-weight: 500;
        }
      }
      .data_unit {
        text-align: left;
        background-color: #3366db;
        padding: 20px;
        border-radius: 4px;
        button {
          margin-top: 10px;
          background: $green;

        }
        .unit_title {
          @include text-mini-black($p500);
          text-transform: uppercase;
          text-align: left;
        }
        .unit_value {
          @include text-title-light($p500);
          font-size: 50px;
          text-align: left;
          margin-bottom: 20px;
          font-size: 50px;
          font-weight: 800;
        }
      }
    }
  }

  .header_menu {
    background: white;
    padding: 0 20px;
    height: 40px;
    .menu_list {
      list-style: none;
      padding: 0;
      margin: 0;
      @include clearfix;
      .list_item {
        float: left;
        margin-right: 50px;
        a {
          @include text-mini-black($p900);
          position: relative;
          line-height: 40px;
          height: 40px;
          text-transform: uppercase;
          display: block;
          &.active {
            color: $accent-500;
              &:after {
              position: absolute;
              content: '';
              bottom: -1px;
              height: 1px;
              width: 100%;
              left: 0;
              background: $accent-500;
            }
          }
        }
      }
    }
  }

  .machines_list {
    margin-top: 40px;
    padding: 0 60px;
    .orchestratorName, .orchestratorXpub {
      input {
        color: $p900;
      }
    }
  }



