.flex_table {
  display: flex;
  flex-flow: column nowrap;
  box-pack: justify;
  justify-content: space-between;
  padding-top: 50px;
  @include text-general($p900);
  border: 1px solid rgba(0, 0, 0, 0.1);
  margin-left: 20px;
  margin-right: 20px;
  .access {
    .table_row-single {
      max-height: 70px;
      line-height: 70px;
      .row_item {
        .item_container {
          h5.user {
            line-height: 30px;
            font-size: 15px;
            font-weight: 500;
            margin-top: 10px;
            i {
              display: block;
              line-height: 20px;
              margin-bottom: 10px;
              font-size: 14px;
              font-weight: 400;
              font-style: normal;
              color: #666;
            }
          }
        }
      }
    }
  }
  // &.static {
  //   position: static;
  //   .table_row-head {
  //     position: static !important;
  //   }
  // }
  .flex-row {
    width: 100%;
    display: flex;
    flex-flow: row nowrap;
    max-height: 50px;
    line-height: 50px;
    padding-left: 10px;
    padding-right: 10px;
    &:hover {
      .row_item.icon {
        .icon_container {
          visibility: visible;
          opacity: 1;
        }
      }
    }
  }
  .flex-item {
    display: flex;
    flex-flow: row nowrap;
    flex-grow: 1;
    flex-basis: 0;
    word-wrap: break-word;
    overflow-wrap: break-word;
    word-break: break-all;
    padding: 0.5em;
    word-break: break-word;
    &.user {
      cursor: pointer;
    }
  }
  .table_row {
    border-bottom: $separator;
    position: relative;
    @include transition(.2s);
    &.activeInfo {
      &:hover {
        background: white !important;
      }
      .table_row-container {
        .table_row-single {
          max-height: 100%;
        }
      }
      .row_item {
        overflow: visible;
        .item_container {
          overflow: visible;
        }
      }
    }
    &.access {
      .table_row-single {
        max-height: 70px;
        line-height: 70px;
      }
    }
    &.hidden {
      display: none;
    }
    &.deactivated {
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      color: rgba(255, 255, 255, 0.25);
      background: rgba(255, 255, 255, 0.07);
      .icon.deactivated {
        display: none !important;
      }
      &:hover {
        color: rgba(255, 255, 255, 0.25);
        background: rgba(255, 255, 255, 0.07);
      }
    }
    &:hover {
      background-color: $p500;
    }
    // .table_row-container {
