    .table_row-head {
      @include text-mini-black($p900);
      background: white;
      letter-spacing: 2px;
      /* height: 30px; */
      line-height: 30px;
      border-bottom: 1px solid rgba(245, 245, 245, 0.1);
      position: absolute;
      z-index: 1;
      top: -49px;
      width: auto;
      padding-top: 20px;
      left: 0;
      right: 0;
      text-transform: uppercase;
    }
