    .row_item {
      padding: 0 5px;
      @include truncate;
      // &.amount {
      //   span {
      //     float: left;
      //   }
      // }
      .item_container {
        width: 100%;
        position: relative;
        @include truncate;

        h5.user {
          line-height: 30px;
          font-size: 14px;
          font-weight: 500;
          i {
            display: block;
            line-height: 10px;
            margin-bottom: 10px;
            font-size: 13px;
            font-weight: 400;
            font-style: normal;
            color: #666;
          }
        }
        &.access {
          font-size: 13px;
          font-weight: 600;
          text-decoration: underline;
          letter-spacing: .5px;
        }
        &.ORCHESTRATED {
          @include text-mini-black($green);
          text-transform: capitalize;
          position: relative;
          padding-left: 15px;
          line-height: 50px;
          &:before {
            position: absolute;
            content: "";
            width: 8px;
            left: 0px;
            top: 20px;
            height: 8px;
            display: block;
            border-radius: 100px;
            background-color: $green;
          }
        }
        &.IMPRINTED {
          @include text-mini-black(#e26d5a);
          text-transform: capitalize;
          position: relative;
          padding-left: 15px;
          line-height: 50px;
          &:before {
            position: absolute;
            content: "";
            width: 8px;
            left: 0px;
            top: 20px;
            height: 8px;
            display: block;
            border-radius: 100px;
            background-color: #e26d5a;
          }
        }
        // .amount_button {
        //   font-size: 11px;
        //   float: left;
        //   margin: 0;
        //   line-height: 18px;
        //   margin-left: 10px;
        //   background-color: transparent;
        //   vertical-align: text-bottom;
        //   @include transition(.3s);
        //   position: relative;
        //   width: auto;
        //   line-height: 26px;
        //   margin-top: 10px;
        //   // &.Confirmed {
        //   //   display: inline-block;
        //   // }
        //   &.Unconfirmed {
        //     display: none !important;
        //   }
        //   &:after {
        //     position: absolute;
        //     content: '';
        //     width: 100%;
        //     height: 3px;
        //     background: $accent-500;
        //     bottom: 0;
        //     left: 0;
        //   }
        //   &:hover {
        //     border-color: $accent-500;
        //     color: $accent-500;
        //     background-color: transparent;
        //   }
        // }
        &>a {
          color: $p100;
          position: relative;
          display: inline-block;
          &:after {
            content: "";
            display: block;
            position: absolute;
            width: 0;
            height: 2px;
            background-color: $p100;
            bottom: 0px;
            @include transition(.2s);
          }
          &:hover {
            &:after {
              width: 100%;
            }
          }
        }
      }
      &.select {
        max-width: 35px;
        // display: none;
        &.active {
          display: inline-block;
        }
        .item_container {
          .checkbox {
            &.CREATED, &.ORCHESTRATED {
              visibility: hidden;
              opacity: 0;
              pointer-events: none;
              position: relative;
              /* use !important to prevent issues with browser extensions that change fonts */
            font-family: 'uniquid' !important;
            speak: none;
            font-style: normal;
            font-weight: normal;
            font-variant: normal;
            text-transform: none;
            line-height: 1;

            /* Enable Ligatures ================ */
            letter-spacing: 0;
            -webkit-font-feature-settings: "liga";
            -moz-font-feature-settings: "liga=1";
            -moz-font-feature-settings: "liga";
            -ms-font-feature-settings: "liga" 1;
            font-feature-settings: "liga";
            -webkit-font-variant-ligatures: discretionary-ligatures;
            font-variant-ligatures: discretionary-ligatures;

            /* Better Font Rendering =========== */
            -webkit-font-smoothing: antialiased;
            -moz-osx-font-smoothing: grayscale;
            }
            &.CREATED {
              visibility: visible;
              opacity: 1;
              font-size: 16px;
              width: auto;
              color: $red;
              display: block;
              padding-left: 4px;
              label {
                &:before {
                  position: static;
                }
                &:after {
                  position: static;
                }
                position: static;
              }
              &:before {
              position: absolute;
              content: '\e117';
              }
            }
            &.ORCHESTRATED {
              visibility: visible;
              opacity: 1;
              font-size: 16px;
              width: auto;
              color: $green;
              display: block;
              padding-left: 4px;
              label {
                &:before {
                  position: static;
                }
                &:after {
                  position: static;
                }
                position: static;
              }
              &:before {
              position: absolute;
              content: '\e116';
              }
            }
            //margin-top: 15px;
          }
        }
      }

      &.id {
        max-width: 300px;
        min-width: 60px;
        .item_container {
          @include truncate;
          overflow: visible;
          text-align: left;
          font-style: italic;
          padding-right: 10px;
          width: 250px;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;

        }
      }
      &.name {
        min-width: 160px;
        max-width: 220px;
      }
      &.operation_date {
        min-width: 100px;
        max-width: 180px;
      }
      &.operation_status {
        min-width: 130px;
        max-width: 130px;
      }
      &.operation_from {
        min-width: 100px;
        max-width: 100px;
      }
      &.operation_address {
        min-width: 350px;
        max-width: auto;
      }
      &.operation_fiat {
        min-width: 160px;
        max-width: 160px;
      }
      &.type {
        min-width: 160px;
        max-width: 300px;
      }
      &.provider {
        min-width: 240px;
        max-width: 240px;
      }
      &.attributes {
        min-width: 240px;
        max-width: 240px;
      }
      &.recipe {
        min-width: 160px;
        max-width: 200px;
      }
      &.orchestrated {
        min-width: 120px;
        max-width: 120px;
        text-align: center;
        .icon-check2 {
          color: $green;
          font-size: 16px;
        }
        .icon-cross2 {
          color: $red;
          font-size: 16px;
        }
      }
      &.relations {
        max-width: 150px;
        min-width: 150px;
        text-align: left;
      }
      &.status {
        max-width: 120px;
        min-width: 200px;

        &.ORCHESTRATED {
          @include text-mini-black($green);
        }
        .item_container {
          span {
            text-transform: uppercase;
            @include text-mini-black($green);
            position: relative;
            &.Revocated {
              color: $accent-700;
              &:after {
                content: "";
                position: absolute;
                height: 2px;
                width: 100%;
                background-color: $accent-700;
                bottom: -5px;
                left: 0;
              }
            }
            &.Active {
              color: $green;
              &:after {
                content: "";
                position: absolute;
                height: 2px;
                width: 100%;
                background-color: $green;
                bottom: -5px;
                left: 0;
              }
            }
            &.Expiring {
              color: #f08421;
              &:after {
                content: "";
                position: absolute;
                height: 2px;
                width: 100%;
                background-color: #f08421;
                bottom: -5px;
                left: 0;
              }
            }
            &.Expired {
              color: #e72025;
              &:after {
                content: "";
                position: absolute;
                height: 2px;
                width: 100%;
                background-color: #e72025;
                bottom: -5px;
                left: 0;
              }
            }
          }
        }
      }
      &.balance {
        min-width: 160px;
        max-width: 160px;
      }
      &.born,
      &.expire {
        max-width: 120px;
        min-width: 120px;
        text-align: left;
      }
      &.icon.Unconfirmed {
        .icon-trash {
          display: none !important;
        }
      }
      &.icon {
        min-width: 170px;
        overflow: visible;
        @include clearfix;
        .item_container {
          overflow: visible;
        }
        .icon_container {
          max-height: 50px;
          visibility: hidden;
          opacity: 0;
          overflow: visible;
          @include clearfix;
          @include transition(.2s);

          // .node_actions {
            // &:hover {
            //   .icon_dropdown {
            //     opacity: 1;
            //     visibility: visible;
            //     top: 50px;
            //   }
            // }
          // }
          .container {
            float: left;
            //width: 50px;
            height: 50px;
            border-radius: 50%;
            cursor: pointer;
            text-align: center;
            position: relative;
            @include transition(.2s);
            &.deactive {
              cursor: default;
              span {
                color: $p300;
              }
              &:hover {
                color: $p300;
                &:before {
                  display: none;
                }
              }
            }

            [class^="icon-"], [class*=" icon-"] {
              font-size: 16px;
              line-height: 50px;
              display: block;
              text-align: center;
              position: relative;
              z-index: 1;
              width: 50px;
              display: inline-block;
              margin-right: 10px;
              // &:before {
              //   content: "";
              //   display: block;
              //   position: absolute;
              //   top: 9px;
              //   left: 50%;
              //   margin-left: -16px;
              //   width: 30px;
              //   height: 30px;
              //   border-radius: 50%;
              //   background: $p100;
              //   visibility: hidden;
              //   opacity: 0;
              //   @include transition(.2s);
              // }
              &:hover {
                color: $accent-500;
              }
                // &:before {
                //   opacity: 1;
                //   visibility: visible;
                // }
              // }
            }

          }
          // .icon_dropdown {
          //   @include dropdown;
          //   top: 60px;
          //   left: -67px;
          //   z-index: $zmax;
          //   width: 180px;
          //   .list_item.add {
          //     .icon-plus {
          //       font-weight: 900;
          //       font-size: 15px;
          //       padding-left: 4px;
          //     }
          //   }
          //   .list_item.view {
          //     border-bottom: 0;
          //   }
          //   .list_item.delete {
          //     background: $red;
          //     color: $p100;
          //     i {
          //       color: $p100;
          //     }
          //   }
          // }
        }
      }
    }
    &:last-child {
      border-bottom: none;
    }
    .table_row {
      &.createAccess {
        margin-top: 10px;
        background: #F2F2F2;
        .provider {
          overflow: visible;
          .item_container {
            overflow: inherit;
            margin-top: 15px;
            .Select-menu-outer {
              z-index: 9999999;
            }
            .Select {
              line-height: 36px;
              z-index: 9999999;
              position: relative;
              input {
                width: 2px;
                border: none;
                text-indent: 0;
              }
            }
          }
        }
      }
    }
    .inside {
      .flex_table {
        margin-bottom: 10px;
        border: none;
        background: transparent;
        .table_row {
          .table_row-head {
            background: #E9E9E9;
          }
        }
        .table_row.inside {
          background: #F2F2F2;
        }
      }
      .recipe {
        a {
          color: #333;
          font-weight: 700;
          letter-spacing: 1px;
          text-decoration: underline;
        }
      }
      .attributes {
        .attribute {
          width: 20px;
          height: 20px;
          display: inline-block;
          margin-right: 10px;
          border-radius: 2px;
          background: #f7f7f7;
          border: 1px solid #c8c8c8;
          text-align: center;
          //line-height: 28px;
          font-size: 13px;
          font-weight: 500;
          &.active {
            background: $green;
            color: white;
            border: 1px solid #21AE6E;
          }
        }
      }
      .provider {
        input {
          height: 40px;
          border: 1px dashed #c8c8c8;
          text-indent: 10px;
          width: 100%;
        }
      }
      .actions {
        button.good {
          width: 100%;
          background: $green;
          border: 1px solid #21AE6E;
        }
        button.gray {
          background: #EBEBEB;
          border: 1px solid #C9C9C9;
          margin-right: 10px;
          color: #3F3F3F;
        }
        button.evil {
          background: #EF5050;
          border: 1px solid #A71919;
        }
      }
    }
  }
}

